@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans.woff") format("woff");
  src: url("../fonts/ptsans.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsansbold.woff") format("woff");
  src: url("../fonts/ptsansbold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.heading {
  margin: 0;
  padding: 26px 18px;
  text-align: center;
}
@media (min-width: 768px) {
  .heading {
    padding: 47px 100px 55px 100px;
  }
}
@media (min-width: 1200px) {
  .heading {
    padding: 59px 300px 44px 300px;
  }
}

.heading__title {
  margin: 0 0 35px 0;
  padding: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .heading__title {
    margin: 0 0 30px 0;
    font-size: 21px;
  }
}

.heading__description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  mix-blend-mode: normal;
}

.advantages {
  width: 100%;
  margin: 0;
  text-align: center;
  text-transform: uppercase;
}

.advantages__name {
  margin: 0 0 35px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.advantages__intro {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}

.advantages__card {
  width: 100%;
}
@media (min-width: 768px) {
  .advantages__card {
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

@media (min-width: 768px) {
  .advantages__card--second {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
@media (min-width: 768px) {
  .advantages__card--second .advantages__card-content {
    order: 2;
  }
}

.advantages__card-content {
  margin: 0;
  padding: 59px 18px 35px 18px;
  color: #ffffff;
  background-color: #81b3d2;
}
.advantages__card-content--clip {
  clip-path: polygon(51% 13%, 100% 5%, 100% 99%, 0 99%, 0 5%);
}
@media (min-width: 768px) {
  .advantages__card-content--clip {
    padding: 52px 33px 31px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__card-content--clip {
    padding: 52px 60px 51px 60px;
  }
}
@media (min-width: 768px) {
  .advantages__card-content {
    clip-path: none;
  }
}
.advantages__card-content--second {
  padding: 37px 18px;
}
@media (min-width: 768px) {
  .advantages__card-content--second {
    padding: 52px 33px 17px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__card-content--second {
    padding: 52px 50px 51px 50px;
  }
}

.advantages__card-title {
  margin: 0 0 11px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .advantages__card-title {
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
  .advantages__card-title {
    margin: 0 75px 11px 75px;
  }
}
@media (min-width: 1200px) {
  .advantages__card-title--clip {
    margin: 0 60px 11px 60px;
  }
}

.advantages__number {
  margin: 0 0 20px 0;
  font-size: 14px;
  line-height: 21px;
}
@media (min-width: 768px) {
  .advantages__number {
    margin-bottom: 24px;
  }
}

.advantages__description {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}
@media (min-width: 768px) {
  .advantages__description--second {
    margin: 0 18px 0 18px;
  }
}
@media (min-width: 1200px) {
  .advantages__description--second {
    margin: 0 26px 0 26px;
  }
}

.advantages__image {
  display: block;
  width: 100%;
  height: 260px;
  background-color: #81b3d2;
  object-fit: cover;
}

.advantages__details-list {
  display: grid;
  margin: 0;
  padding: 23px 18px 44px 18px;
  list-style: none;
  color: #000000;
  background-color: #ffffff;
  gap: 26px;
}
@media (min-width: 768px) {
  .advantages__details-list {
    padding: 0;
    gap: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__details-item {
  margin: 0;
  padding: 8px 0 0 47px;
  text-align: left;
}
@media (min-width: 768px) {
  .advantages__details-item {
    padding: 155px 33px 60px 33px;
    text-align: center;
  }
}
@media (min-width: 1200px) {
  .advantages__details-item {
    padding: 169px 50px 69px 50px;
  }
}

.advantages__details-item--housing {
  background-image: url("../img/icons/house.svg");
  background-repeat: no-repeat;
  background-size: 39px 37px;
}
@media (min-width: 768px) {
  .advantages__details-item--housing {
    background-position: 50% 60px;
    background-size: 77px 72px;
  }
}
@media (min-width: 1200px) {
  .advantages__details-item--housing {
    padding-top: 165px;
    background-size: 78px 73px;
  }
}

.advantages__details-item--food {
  background-image: url("../img/icons/burger.svg");
  background-repeat: no-repeat;
  background-size: 40px 35px;
}
@media (min-width: 768px) {
  .advantages__details-item--food {
    padding-top: 150px;
    background-position: 50% 62px;
    background-size: 80px 68px;
  }
}
@media (min-width: 1200px) {
  .advantages__details-item--food {
    padding-top: 162px;
    background-position: 50% 70px;
    background-size: 73px 69px;
  }
}

.advantages__details-item--presents {
  background-image: url("../img/icons/gift.svg");
  background-repeat: no-repeat;
  background-size: 32px 38px;
}
@media (min-width: 768px) {
  .advantages__details-item--presents {
    padding-top: 150px;
    background-position: 50% 62px;
    background-size: 67px 75px;
  }
}
@media (min-width: 1200px) {
  .advantages__details-item--presents {
    padding: 165px 70px 69px 70px;
    background-position: 50% 69px;
    background-size: 77px 75px;
  }
}

.advantages__details-title {
  margin: 0 0 16px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .advantages__details-title {
    font-size: 21px;
  }
}
@media (min-width: 768px) {
  .advantages__details-title--housing {
    margin-bottom: 20px;
  }
}
.advantages__details-title--food {
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .advantages__details-title--food {
    margin-bottom: 24px;
  }
}
.advantages__details-title--presents {
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .advantages__details-title--presents {
    margin-bottom: 22px;
  }
}

.advantages__details-description {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}

.advantages__list {
  display: grid;
  margin: 0;
  padding: 24px 26px 0 26px;
  list-style: none;
  color: #000000;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__list {
    padding: 0;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item {
  position: relative;
  padding: 52px 33px;
}
.advantages__item::after:not(:last-of-type) {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .advantages__item::after:not(:last-of-type) {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    margin: 0;
    padding: 52px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    padding: 63px 50px 41px 50px;
  }
}

@media (min-width: 768px) {
  .advantages__item--center {
    padding: 41px 33px;
  }
}
@media (min-width: 1200px) {
  .advantages__item--center {
    padding: 52px 50px 51px 50px;
  }
}

.advantages__list .advantages__item:not(:last-child) {
  border-bottom: 1px solid #d6d6d6;
}
@media (min-width: 768px) {
  .advantages__list .advantages__item:not(:last-child) {
    border: none;
  }
}

.advantages__heading {
  margin: 0 0 11px 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .advantages__heading {
    margin: 0 20px 11px 20px;
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
  .advantages__heading {
    margin: 0 80px 11px 80px;
  }
}

.booking {
  margin: 0 0 10px 0;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  background-color: #ffffff;
}

.booking__wrapper {
  padding: 38px 18px 49px 18px;
}
@media (min-width: 768px) {
  .booking__wrapper {
    padding: 61px 155px 58px 155px;
  }
}
@media (min-width: 1200px) {
  .booking__wrapper {
    padding: 61px 371px 66px 371px;
  }
}

.booking__heading {
  margin: 0 0 20px 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-align: center;
}
@media (min-width: 768px) {
  .booking__heading {
    margin-bottom: 30px;
    font-size: 30px;
  }
}
@media (min-width: 1200px) {
  .booking__heading {
    margin-bottom: 35px;
  }
}

.booking__description {
  margin: 0 0 32px 0;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
@media (min-width: 768px) {
  .booking__description {
    margin: 0 0 45px 0;
  }
}

.button-link {
  display: block;
  width: 100%;
  padding: 16px 49px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: inherit;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button-link:hover {
  background-color: #669ec0;
}
.button-link:active, .button-link:focus {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
  outline: none;
}
.button-link:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
}

.booking__map {
  position: relative;
  height: 350px;
  margin-bottom: 10px;
}

.booking__map-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: auto;
  max-height: 350px;
  background-color: #ffffff;
  object-fit: cover;
}

.booking__map-iframe {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  max-height: 350px;
  border: none;
  object-fit: cover;
}

.gallery {
  width: 100%;
}

.gallery__header {
  margin: 0;
}
@media (min-width: 768px) {
  .gallery__header {
    padding: 67px 100px 80px 100px;
  }
}
@media (min-width: 1200px) {
  .gallery__header {
    padding: 97px 382px 90px 380px;
  }
}

@media (min-width: 768px) {
  .heading__title-gallery {
    font-size: 30px;
  }
}

.heading__description-gallery {
  text-align: center;
}
@media (min-width: 768px) {
  .heading__description-gallery {
    margin-right: 71px;
    margin-left: 71px;
  }
}
@media (min-width: 1200px) {
  .heading__description-gallery {
    margin: 0;
  }
}

.gallery__main-img {
  margin: 0;
}

.gallery__content {
  position: relative;
  margin: 0;
  padding: 25px 0 27px 0;
}
.gallery__content::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: -20px;
  width: calc(100% + 36px);
  height: 1px;
  background-color: #e5e5e5;
}
@media (min-width: 768px) {
  .gallery__content::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .gallery__content {
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .gallery__content {
    position: relative;
    display: flex;
    height: 100%;
    padding-top: 187px;
  }
  .gallery__content--main {
    padding-top: 320px;
  }
}

.gallery__image-main {
  display: block;
  width: 100%;
  max-height: 400px;
  margin: 0;
  object-fit: cover;
}
@media (min-width: 768px) {
  .gallery__image-main {
    margin: 0 0 15px 0;
  }
}
@media (min-width: 1200px) {
  .gallery__image-main {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
}

@media (min-width: 768px) {
  .gallery__content-description {
    display: flex;
    justify-content: space-between;
    padding: 22px 20px 15px 20px;
    background-color: #f2f2f2;
    gap: 40px;
  }
}
@media (min-width: 1200px) {
  .gallery__content-description {
    position: relative;
    z-index: 1;
    flex-grow: 1;
    align-items: start;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.3);
  }
}

.gallery__wrapper {
  margin: 0;
  padding: 0 20px 0 20px;
}
@media (min-width: 768px) {
  .gallery__wrapper {
    padding: 0 60px 0 60px;
  }
}
@media (min-width: 1200px) {
  .gallery__wrapper {
    padding: 0;
  }
}

.gallery__heading {
  margin: 0 0 2px 0;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.gallery__author {
  margin: 0 0 11px 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: none;
}
@media (min-width: 768px) {
  .gallery__author {
    margin: 0;
  }
}

.gallery__content-likes {
  display: flex;
  align-self: center;
  margin-top: 2px;
}

.gallery__likes--icon {
  margin: auto 14px auto 0;
  padding: 0;
  color: #000000;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .gallery__likes--icon {
    margin-right: 9px;
  }
}
@media (min-width: 1200px) {
