.gallery__likes-text {
  font-size: 14px;
}

.gallery__image {
  display: block;
  width: 100%;
  max-height: 400px;
  margin: 0 0 15px 0;
  background-color: #333333;
  object-fit: cover;
}
@media (min-width: 768px) {
  .gallery__image {
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .gallery__image {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
}

.gallery__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .gallery__list {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .gallery__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.gallery__item {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main-container {
  width: 100%;
}

